





























































































import Vue from "vue";
import { createSimpleTransition } from 'vuetify/src/util/helpers';

import RomaActivityModalForm from "@/components/RomaActivityModalForm.vue";
import RomaActivity from "@/components/RomaActivity.vue";
import RomaConfirmDialog from '@/components/partial/RomaConfirmDialog.vue';
import UnitPlanActivity from "@/models/UnitPlanActivity";
import UnitPlanActivityDua from '@/models/UnitPlanActivityDua';
import UnitPlanActivityFile from '@/models/UnitPlanActivityFile';
import UnitPlanActivityIe from '@/models/UnitPlanActivityIe';

const romaActivityListTransition = createSimpleTransition('activity-list-transition');
Vue.component('my-transition', romaActivityListTransition);

export default Vue.extend({
    name: "RomaActivityList",
    props: {
      unit: {
          type: Object,
          required: true,
      },
    },
    data() {
        return {
            dialog: false,
            dialogDeleteActivity: false,
            selectedActivity: null,
        };
    },
    computed: {
      activities () {
          const activities = UnitPlanActivity.query().
                              where('unitplan_id', this.unit.id).
                              with('duas').
                              with('files').
                              with('ies').
                              get();
          return activities;
      },
    },
    methods: {
        addActivity () {
            this.selectedActivity = null; // Reinicio actividad seleccionada si es nueva
            this.dialog = true;
        },
        editActivity (activity: Object) {
          this.selectedActivity = activity;
          this.dialog = true;
        },
        goDelActivity(activity: object) {
            this.dialogDeleteActivity = true;
            this.selectedActivity = activity;
        },
        delActivity () {
            UnitPlanActivity.delete(this.selectedActivity.id);
            UnitPlanActivityDua.delete(detail => detail.activity_id === this.selectedActivity.id);
            UnitPlanActivityFile.delete(detail => detail.activity_id === this.selectedActivity.id);
            UnitPlanActivityIe.delete(detail => detail.activity_id === this.selectedActivity.id);
            this.dialogDeleteActivity = false;
        } ,
        style (itemIndex: number) {
            const colors = ['--v-roma_rcolor-base', '--v-primary-base', '--v-secondary-base', '--v-roma_acolor-base'];
            let color = colors[itemIndex];
            color = `var(${colors[itemIndex % colors.length]})`;

            return color;
        }
    },
    components: {
        RomaConfirmDialog,
        RomaActivityModalForm,
        RomaActivity
    }
});
