











































































































































































import Vue from 'vue';
import { Ripple } from 'vuetify/lib/directives/';
import UnitPlanOa from '@/models/UnitPlanOa';
import UnitPlanActivity from '@/models/UnitPlanActivity';
import UnitPlanActivityIe from '@/models/UnitPlanActivityIe';
import UnitPlanActivityFile from '@/models/UnitPlanActivityFile';
import UnitPlanActivityDua from '@/models/UnitPlanActivityDua';
import DuaCategory from "@/models/DuaCategory";
import Oa from "@/models/Oa";
import groupBy from 'lodash/groupBy';

function requestDefaultValues () {
    return {
        title: '',
        selectedIEs: [],
        activityLer: '',
        files: [],
        selectedDuaCategory: null,
        selectedDuas: [],
        activityEva: {
            type: null,
            instrument: null,
            text: '',
        }
    }
}

export default Vue.extend({
    name: 'RomaActivityModalForm',
    props: {
        value: Boolean,
        activity: Object,
        maxWidth: {
            type: String,
            default: '90%',
        },
        overlayOpacity: {
            type: String,
            default: '0.8',
        },
        persistent: {
            type: Boolean,
            default: true,
        },
        unit: {
            type: Object,
            required: true,
        },
        selected: {
            type: Array
        }
    },
    data() {
        return {
            valid: false,
            loading: false,
            showing: this.value,
            evaluationTypes: ['Sumativa', 'Formativa', 'Diagnóstica'],
            instrumentTypes: ['Rúbrica', 'Escala de apreciación Numérica', 'Pautas de Cotejo', 'Pauta de Corrección'],
            request: requestDefaultValues(),
        };
    },
    computed: {
        unitOAs () {
            const details = UnitPlanOa.query()
                .where('unitplan_id', this.unit.id)
                .with('oa', query => {
                    query.with('ies')
                })
                .get();

            return details.map(detail => detail.oa);
        },
        duas () {
            let menu = [];
            DuaCategory.query().with('duas').get().forEach(category => {
                menu.push({header: category.name});
                category.duas.forEach(dua => {
                    menu.push({id: dua.id, name: dua.name, cod: dua.cod})
                })
            });
            return menu;
        },
    },
    watch: {
        activity (value) {
            if (value) {
                this.request.title = value.title;
                this.request.activityLer = value.activityLer;
                this.request.activityEva.text = value.activityEva;
                this.request.activityEva.type = value.type;
                this.request.activityEva.instrument = value.instrument;

                this.request.selectedIEs = value.ies;


                if (value.duas.length > 0) {
                    this.request.selectedDuaCategory = this.duas.find(cat => cat.id === value.duas[0].duacategory_id);
                }
                this.request.selectedDuas = value.duas;
                this.request.files = value.files;
            } else {
                this.request = requestDefaultValues();
            }
        },
        selected (value) {
            if (value.length > 0) {
                this.request.selectedIEs = [];
                value.forEach(oa => {
                    if (oa.selectedRows.length > 0) {
                        oa.selectedRows.forEach(ie => {
                            this.request.selectedIEs.push(ie);
                        })
                    }
                })
            }
        }
    },
    methods: {
        show(value: Boolean) {
            this.$emit('input', value);
            this.request = requestDefaultValues();
        },
        save () {
            let data = {
                unitplan_id: this.unit.id,
                title: this.request.title,
                activityLer: this.request.activityLer, // Actividad aprendizaje
                // Actividad Instrumento
                activityEva: this.request.activityEva.text,
                type: this.request.activityEva.type,
                instrument: this.request.activityEva.instrument,

            };
            if (this.activity) data.id = this.activity.id; // Si va a editar

            UnitPlanActivity.insertOrUpdate({data: data}).then(response => { // Si ya se inserto la actividad
                // IEs
                if (this.request.selectedIEs.length > 0) {
                    // Si actualiza borro ies pertenecientes a la actividad
                    if (this.activity) UnitPlanActivityIe.delete(activityOa => activityOa.activity_id === this.activity.id);
                    this.request.selectedIEs.forEach(ie => {
                        UnitPlanActivityIe.insert({
                            data: {
                                activity_id: response.unitplan_activities[0].id,
                                ie_id: ie.id,
                            }
                        })
                    });
                }
                // Duas
                if (this.request.selectedDuas.length > 0) {
                    // Si actualiza borro duas pertenecientes a la actividad
                    if (this.activity) UnitPlanActivityDua.delete(activityDua => activityDua.activity_id === this.activity.id);
                    this.request.selectedDuas.forEach(dua => {
                        UnitPlanActivityDua.insert({
                            data: {
                                activity_id: response.unitplan_activities[0].id,
                                dua_id: dua.id,
                            }
                        })
                    });
                }
                // Files
                this.request.files.forEach(file => {
                    UnitPlanActivityFile.insertOrUpdate({
                        data: {
                            activity_id: response.unitplan_activities[0].id,
                        }
                    })
                });

                this.$emit('activity-added');
                this.request = requestDefaultValues();
                this.show(false);
            })
        }
    },
    directives: {
        Ripple
    }
});
