





















































import Vue from 'vue';
import { Ripple } from 'vuetify/lib/directives/';

function requestDefaultValues () {
    return {}
}

export default Vue.extend({
    name: 'RomaMessageModal',
    props: {
        value: Boolean,
        title: {
            type: String,
            default: 'Comentar'
        },
        maxWidth: {
            type: String,
            default: '90%',
        },
        overlayOpacity: {
            type: String,
            default: '0.8',
        },
        persistent: {
            type: Boolean,
            default: true,
        },
        messages: {
            type: Array,
            default () {
                return [];
            }
        }
    },
    data() {
        return {
            valid: false,
            loading: false,
            showing: this.value,
            message: '',
            comments: this.messages
        };
    },
    computed: {

    },
    methods: {
        show(value: Boolean) {
            this.$emit('input', value);
        },
        comment () {
            let data = {user: {name: '', avatar: 'http://i.pravatar.cc/64'}, message: this.message, date: new Date()};
            this.comments.push(data);
            this.message = '';
        }
    },
    directives: {
        Ripple
    }
});
