



























import Vue from "vue";
import highlighter from 'vue-highlight-words'
import {mapState} from "vuex";
import groupBy from 'lodash/groupBy';
import Oa from '@/models/Oa';

export default Vue.extend({
    name: "RomaActivityList",
    props: {
        activity: {
            type: Object,
            required: true
        },
        color: {
            type: String,
            default: 'grey'
        },
    },
    data() {
        return {};
    },
    computed: {
      ...mapState({
          highlightWords (state) {
              return state.highlight.split(' ');
          }
      })
    },
    methods: {
      groupByOa (ies) {
          const oas = groupBy(ies, n => {
              return n.oa_id;
          });
          return Oa.findIn(Object.keys(oas));
      }
    },
    components: {
        highlighter
    }
});
